import React, { Component, useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import {Link, useHistory, withRouter} from 'react-router-dom';
import { listTechDetails } from '../actions/tzAction';
import Loader from './Loader';
import Message from './Message';
/*let data = 
    {'date': '12.01.2021', 
    'org': ' 8', 
    "o_id": '1',
    'tz_id': '4368', 
    'end_date': '26.01.2021', 
    'proj': ' 6040', 
    'group': '', 
    'kind': '', 
    'type': '', 
    'task': ' ', 
    'tz_st': '', 
    'tender_st': ' ', 
    'count': '', 
    'cp_st': ' ',
    'pay_cond': '100% ',
    'private': '', 
    'info': `    150 .        (. ).      4- `,
    "docs": [".pdf", "1.doc"],
    "costs": [
        {
            "task": " ",
            "metr": ".",
            "count": 150
        },
        {
            "task": "",
            "metr": "",
            "count": 1
        }
    ], 
    "calendars":[
        {
            "name": " ",
            "period": 1,
            "term": 7
        },
        {
            "name": " ",
            "period": 2,
            "term": 9
        },
        {
            "name": "",
            "period": 1,
            "term": 10
        }
    ], 
    "history": "" 
    }*/

const Tec = ({match}) => {
  const dispatch = useDispatch()
  const history = useHistory()
  const tzDetails = useSelector(state => state.tzDetails)
  const {loading, error, tech} = tzDetails
  const tzList = useSelector(state => state.tzList)
  const data = tzList

  const [cal, setCal] = useState([])
  const [cps, setCps] = useState([])
  let last = 0


  useEffect(() => {
    dispatch(listTechDetails(match.params.tz_id))
  }, [dispatch, match])

  useEffect(() => {
    setCal(tech.cal)
    setCps(data.cps)
})
  
  const userInfo = localStorage.getItem('userInfo') ? JSON.parse(localStorage.getItem('userInfo')) : null

  Date.prototype.getWeek = function() {
    var date = new Date(this.getTime());
    date.setHours(0, 0, 0, 0);
    date.setDate(date.getDate() + 3 - (date.getDay() + 6) % 7);
    var week1 = new Date(date.getFullYear(), 0, 4);
    return 1 + Math.round(((date.getTime() - week1.getTime()) / 86400000 - 3 + (week1.getDay() + 6) % 7) / 7);
  }

    let onClickAdd = (e) => {
      history.push(`/cps/create/${match.params.tz_id}`)
    }
    let onClickUpdate = (e) => {
      history.push(`/techs/upd/${match.params.tz_id}`)
     }
    
    return(
        <div>
          <div>
        <h4 id="name" className="text-center"> </h4>
        <h5> </h5>
        <div>
        {loading ? <Loader/> : error ? <Message variant="danger">{error}</Message> : 
        <table className="table w-50 one_item" >
          <thead>
          </thead>
          <tbody>
            <tr>
              <td scope="col" colSpan='2'><h5> </h5></td>
            </tr>
            <tr>
              <td scope="col"></td>
              <td scope="col">
              <Link to={`/orgs/link/${tech.o_id}`} >
                  {tech.client}
                  </Link>
                  </td>
            </tr>
            <tr>
              <td scope="col"></td>
              <td scope="col">{tech.proj}</td>
            </tr>
            <tr>
              <td scope="col"> </td>
              <td scope="col">{tech.group}</td>
            </tr>
            <tr>
              <td scope="col"> </td>
              <td scope="col">{tech.type}</td>
            </tr>
            <tr>
              <td scope="col"> </td>
              <td scope="col">{tech.kind}</td>
            </tr>
            <tr>
              <td scope="col"> </td>
              <td scope="col">{tech.task}</td>
            </tr>
            <tr>
              <td scope="col"> </td>
              <td scope="col">{tech.pay_cond}</td>
            </tr>
            <tr>
              <td scope="col">   </td>
              <td scope="col">{tech.date}</td>
            </tr>
            <tr>
              <td scope="col">   </td>
              <td scope="col">{tech.end_date}</td>
            </tr>
            <tr>
              <td scope="col">   </td>
              <td scope="col">{ tech.privacy ? "  " : ""}</td>
            </tr>
            <tr>
              <td scope="col"> </td>
              <td scope="col">{Date.parse(tech.end_date)> Date.now() ? '' : ''}</td>
            </tr>
          </tbody>
        </table>}
        </div>
          <h5 id="name" className="text-justify"></h5>
          {tech.docs ? tech.docs.map((item, i)=>{
        return (
          <p className="text-justify">{item}</p>
       )}) : <p className="text-justify"> </p>}
          
          {Date.parse(tech.end_date)> Date.now() ? userInfo.o_id == tech.o_id ? 
          <button type="button" className="btn btn-outline-dark" onClick={onClickUpdate}></button> :
          userInfo.group_id == 2 || userInfo.group_id == 3?
          <button type="button" className="btn btn-outline-dark" onClick={onClickAdd}> </button> : <p></p>
          :<p>   </p>}

          <h5 className="text-justify"> </h5>
          <p className="text-justify">{tech.info}</p>

          <h5 className="text-justify"> </h5>
          <table className="table w-25" id="org_table">
    <thead>
      <tr className="org_head">
        <th scope="col"> </th>
        <th scope="col"> </th>
        <th scope="col">-</th>
      </tr>
    </thead>
    <tbody>
      {tech.cst ? tech.cst.map((item, i)=>{
        return (
      <tr>
        <td>{item.task}</td>
        <td>{item.metr}</td>
        <td>{item.count}</td>
      </tr>)}): <p>    </p>}
    </tbody>
  </table> 


  <h5 className="text-justify">  </h5>
          <table className="table w-25" id="org_table">
    <thead>
      <tr className="org_head">
        <th scope="col"> </th>
        <th scope="col" colSpan="2"> </th>
      </tr>
      <tr className="org_head">
        <th scope="col"></th>
        <th scope="col">, </th>
        <th scope="col"></th>
      </tr>
    </thead>
    <tbody>
      {cal ? cal.map((item, i)=>{ last = last + item.period
        return (
      <tr>
        <td>{item.task_name}</td>
        <td>{item.period}</td>
        <td>{new Date(tech.end_date).getWeek() + last}</td>
      </tr>)}): <p>    </p>}
    </tbody>
  </table>

  <h5 className="text-justify"> </h5>
  <textarea className='cr_area' value={tech.history} rows="5"></textarea>
</div>
</div>
)
  }

  const Tech =withRouter(Tec) 
  export default Tech;


